* {
  box-sizing: border-box; }

html {
  font-family: 'SF Pro display';
  font-size: 16px; }

body {
  min-height: 100vh;
  padding: 0;
  margin: 0; }

ul, ol, h1, h2, h3 {
  padding: 0;
  margin: 0; }

.page-nav {
  background-color: rgba(45, 45, 45, 0.98);
  display: flex;
  justify-content: center;
  height: 50px;
  padding: 0 8px; }

.page-nav__menu {
  display: flex;
  justify-content: space-between;
  max-width: 960px;
  width: 100%;
  list-style: none; }

.page-nav__link {
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ffffff;
  text-decoration: none; }

.page-sub-nav {
  background-color: #f6f6f6; }

.page-sub-nav__content {
  margin: 0 auto;
  height: 100px;
  display: flex;
  max-width: 1350px;
  justify-content: center; }

.page-sub-nav__content > ul {
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  max-width: 1350px; }

.page-nav__link2 {
  display: flex;
  justify-content: center;
  text-align: center;
  width: 60px;
  padding: 0.5rem;
  box-sizing: content-box; }

.page-nav__link2 > a {
  text-decoration: none;
  font-size: 8px;
  color: rgba(0, 0, 0, 0.8); }

.main {
  background-color: #f7f7f7;
  margin-top: 15px; }

.section-description {
  text-align: center;
  padding-top: 25px; }

.section-content {
  max-width: 1350px;
  margin: 0 auto; }

.section-description > h1 {
  font-size: 3.5rem; }

.section-description > h2 {
  font-size: 2.5rem;
  margin-bottom: 10px; }

.sub-header {
  text-align: center;
  margin: 0 auto;
  color: #888;
  padding-top: 0.75rem; }

.sub-header span {
  padding: 10px; }

.details {
  padding-top: 20px;
  color: #0070c9;
  text-decoration: none;
  font-size: 16px; }

.details > a {
  color: #0070c9;
  text-decoration: none;
  font-size: 20px;
  font-weight: 200; }

.main__img {
  display: block;
  width: 60%;
  min-width: 400px;
  margin: 0px auto;
  padding: 60px; }

.page-nav__switcher {
  background-color: transparent;
  border: none;
  height: 20px;
  width: 20px;
  outline: none;
  font-size: 1rem;
  position: relative; }

.page-nav__switcher:before {
  content: '';
  width: 100%;
  height: 1px;
  background-color: #FFF;
  position: absolute;
  top: 5px;
  left: 0;
  transition: all 200ms; }

.page-nav__switcher:after {
  content: '';
  width: 100%;
  height: 1px;
  background-color: #FFF;
  position: absolute;
  bottom: 5px;
  left: 0;
  transition: all 200ms; }

.page-nav.opened .page-nav__switcher:before {
  transform: translateY(5px) rotate(45deg); }

.page-nav.opened .page-nav__switcher:after {
  transform: translateY(-4px) rotate(-45deg); }

.page-nav__controls {
  display: none;
  justify-content: space-between;
  align-items: center;
  width: 100%; }

.iphone_8 {
  background-color: #f9f0eb;
  margin-top: 25px; }

.iphone8_img {
  text-align: center; }

.iphone8_img img {
  width: 60%;
  padding: 0; }

.compare_models {
  margin-top: 25px; }

.section-content__models {
  display: flex;
  max-width: 1350px;
  margin: 0 auto;
  flex-direction: row-reverse;
  justify-content: flex-end;
  background-color: #fafafa; }

.section-description__iphones {
  display: flex;
  flex-direction: column;
  justify-content: center;
  flex-wrap: wrap;
  padding-left: 30px; }

.section-description__iphones h1 {
  font-size: 45px; }

.compare_img {
  width: 50%;
  padding: 25px 0 0 25px; }

.compare_img img {
  width: 100%; }

.ios_11 {
  margin-top: 25px; }

.section-content__ios_11 {
  background-color: #fafafa;
  display: flex;
  max-width: 1350px;
  margin: 0 auto;
  padding: 0 25px;
  align-items: center; }

.section-description > p {
  font-size: 20px;
  padding: 15px; }

.section_image {
  width: 100%; }

.three_phones {
  display: flex;
  justify-content: flex-end;
  margin: 50px 0; }

.ios__11 {
  width: 80%;
  margin: 0 auto;
  padding: 0 25px; }

.accessories_skins {
  margin-top: 25px;
  margin-bottom: 25px; }

.accessories {
  display: flex;
  text-align: center;
  justify-content: space-between; }

.description_wrap h2,
.description_wrap h2 {
  font-size: 25px; }

.description_wrap h1,
.description_wrap h1 {
  font-size: 35px; }

.airpods_bcg {
  background-image: url(./assets/images/airpods_large_2x.jpg);
  margin-right: 10px; }

.skins_bcg {
  background-image: url(./assets/images/accessories_large_2x.jpg);
  margin-left: 10px; }

.column_1_2 {
  width: 50%;
  height: 400px;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  justify-content: center;
  align-items: center; }

.compare_iphones {
  color: white;
  background-image: url(./assets/images/howtoshoot_large_2x.jpg);
  height: 800px;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.compare_iphones h1 {
  font-size: 3.5rem; }

.pay_icloud {
  margin-top: 25px; }

.pay_img {
  background-image: url(./assets/images/apple_pay_large_2x.jpg);
  height: 400px;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 40px; }

.icloud_img {
  background-image: url(./assets/images/icloud_large_2x.jpg);
  height: 400px;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 40px; }

.payment {
  display: flex;
  text-align: center;
  justify-content: space-between;
  background: #fafafa; }

.column_payment {
  padding-top: 50px;
  width: 45%;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-direction: column;
  justify-content: center; }

.description_wrap p {
  font-size: 20px;
  padding: 0 25px; }

footer {
  background: #f2f2f2;
  height: 200px;
  margin-top: 25px; }

.rights {
  color: #9b9b9b;
  font-size: 0.8rem;
  padding-top: 1rem; }

.footer_menu {
  display: flex;
  list-style: none;
  margin-top: 1rem; }

.footer_menu li {
  font-size: 0.9rem;
  color: #555;
  margin-right: 1rem;
  padding-right: 1rem;
  border-right: 1px solid #d6d6d6; }

.footer_menu li:nth-last-child(1) {
  border-right: none; }

.footer_menu a {
  color: #555;
  text-decoration: none; }

@media (max-width: 767px) {
  .page-nav__controls {
    display: flex; }
  .page-nav.opened {
    background-color: black; }
  .page-nav__menu {
    position: absolute;
    top: 50px;
    left: 0;
    width: 100vw;
    min-height: 100vh;
    background-color: black;
    flex-direction: column;
    justify-content: flex-start;
    padding: 0 45px;
    display: flex;
    visibility: hidden;
    transition: all 200ms;
    transform: scaleY(0);
    transform-origin: top; }
  .page-nav.opened .page-nav__menu {
    visibility: visible;
    transform: scaleY(1); }
  .page-nav__link {
    padding: 15px 0;
    border-bottom: 1px solid #292929;
    opacity: 0;
    transition: opacity 150ms 30ms; }
  .page-nav.opened .page-nav__link {
    opacity: 1; } }
